.box {
    height: 62px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    font-size: 32px;
    font-weight: 400;
    position: relative;
}

.note {
    position: absolute;
    color: rgba(105, 105, 105, 1);
    font-size: 15px;
    font-family: 'Lato';
    top: 100%;
    transform: translateY(2px);
    left: 0;
}