.box {
    height: 74px;
    display: flex;
    align-items: center;
    padding-left: 16px;
    border-width: 2px;
    border-style: solid;
    border-radius: 12px;
    font-size: 32px;
    font-weight: 400;
    position: relative;
}

.note {
    position: absolute;
    padding: 10px;
    color: rgba(105, 105, 105, 1);
    font-size: 15px;
    font-family: 'Lato';
    top: 0;
    transform: translateY(-50%);
    background-color: white;
}

.arrow {
    margin-left: auto;
    margin-right: 16px;
    cursor: pointer;
}