.home {
    box-sizing: border-box;
    width: 100%;
}

.landingPage {
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 62px;
}

.firstLine {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 78px;
    margin-top: 266px;
}

.firstLineItem1 {
    margin-right: 46px;
}

.firstLineItem2 {
    margin-right: 39px;
}

.firstLineItem3 {
    margin-right: 29px;
    color: rgba(0, 0, 0, 1);
    font-size: 32px;
}

.secondLine {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.secondLineItem1 {
    color: rgba(0, 0, 0, 1);
    font-size: 32px;
    font-weight: 400;
    margin-right: 25px;
}

.secondLineItem2 {
    margin-right: 21px;
}

.secondLineItem3 {
    margin-right: 16px;
    color: rgba(0, 0, 0, 1);
    font-size: 32px;
    font-weight: 400;
}

.thirdLine {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: auto;
}

.workPage {
    width: 100%;
    padding: 193px 189px 337px 174px;
}

.work {
    margin-bottom: 107px;
}

.lastWork {
    margin-bottom: 199px;
}

.footnote {
    font-family: 'DM Sans';
    font-weight: 400;
    font-size: 32px;
    margin-bottom: 40px;
}

.footnote:last-of-type {
    margin-bottom: 0;
}

.footnoteText {
    cursor: pointer;
}