.navBar {
    width: 100%;
    height: 79px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding: 0 64px;
    background-color: rgba(255, 255, 255, 0.85);
    position: fixed;
    top: 0;
}

.link {
    all: unset;
    text-decoration: none;
    cursor: pointer;
    color: inherit;
}

.link:not(:last-of-type) {
    margin-right: 86px;
}

.link:first-of-type {
    margin-left: auto;
}